import s from './DiaryDatealendar.module.css';
import DatePicker from 'react-datepicker';
import { ReactComponent as NewCalendar } from 'assets/icons/calendar.svg';
import 'react-datepicker/dist/react-datepicker.css';

const DiaryDatealendar = ({ date, setDate }) => {
  return (
    <div className={s.block}>
      <DatePicker
        className={s.calendar}
        dateFormat="dd/MM/yyyy"
        maxDate={new Date()}
        selected={date}
        onChange={date => setDate(date)}
      />
      <NewCalendar className={s.icon} />
    </div>
  );
};

export default DiaryDatealendar;
